
.homePage {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  margin: 50px 0 0 0;
}

.deposits, .statistics {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.ddArrow {
  border: 2px solid #0033ad;
  border-radius: 2px;
  border-right: 0;
  border-top: 0;
  margin-top: -.4em;
  pointer-events: none;
  transform: rotate(-45deg);
  transform-origin: center;
  height: .625em;
  width: .625em;
  right: 1.125em;
  transition: border-color .15s ease-in-out;
}

