.nav {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  border: 1px solid red;
  height: 50px;
}

#basic-nav-dropdown, .navl {
  text-decoration: none;
}

.tokDropDown, .navdd {
  background-color: #1a1a1a;
  border-radius: 2px;
  width: 100%;
}

.tokDropDown {
  width: 29vw;
  text-align: center;
}

.navd {
  background-color: #1a1a1a;
  width: 100%;
  border: 1px solid red;
  padding: 0 5px;
}

.ddArrow {
  border: 2px solid #0033ad;
  border-radius: 2px;
  border-right: 0;
  border-top: 0;
  margin-top: -.4em;
  pointer-events: none;
  transform: rotate(-45deg);
  transform-origin: center;
  height: .625em;
  width: .625em;
  right: 1.125em;
  transition: border-color .15s ease-in-out;
}

#dd-btn {
  width: 29vw;
  background-color: transparent;
  border: 1px solid #0033ad;
  border-radius: 2px;
  padding: 0 1vw;
  margin: 0 .5vw;
  font-size: 16px;
}

.tokName {
  width: 29vw;
  background-color: #1a1a1a;
  text-decoration: none;
}
.tokName:hover {
  color: #0027a6;
}
